const Project = (props) => {
    return(
        <div className="project-parent">
        <div className="project-container inner-wrapper-align-top">
            <div class="project-image inner">
                <img src={props.image} width="100%"/>
            </div>
            <div class="project-body inner">
                {props.children}
            </div>
        </div>
        </div>
    );
}

export default Project;